<template>
  <div style="padding : 10px">
    <h4>팔로워</h4>
    <input type="text" placeholder="🔍" @input="searchUser" />
    <div class="post-header" v-for="(followerData, i) in sFollower" :key="i">
      <div class="profile" :style="{ backgroundImage: `url(${followerData.image})` }"></div>
      <span class="profile-name">{{ followerData.name }}</span>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref, toRefs, watch } from 'vue'
import axios from 'axios'
import { useStore } from 'vuex'

export default {
  name: "MyPage",
  props: {
    one: Number
  },
  setup(props) {
    // 일반적인 변수 선언법
    let follower = ref([]);
    let sFollower = ref([]);
    // reactive 이런 함수도 있음 => ref랑 거의 똑같은데 관습적으로 ref는 primitive data를 쓰고 reactive는 reference data를 씀
    // 근데 안에 코드를 까보면 어차피 ref랑 reactive랑 차이가 없기 때문에 ref 하나만 써도 상관없음
    // let test = reactive({
    //   name: 'kim'
    // });

    // props 변수 사용법
    // 변수에 중괄호 치는거 => ES6의 Destructuring 문법임
    let { one } = toRefs(props); // props 사용할 땐 toRefs 사용하는게 관습적임
    console.log(`props로 받은 데이터는 ${one.value}입니다`);

    // watch 함수 사용법
    // 첫번째 인자는 watch를 할 변수, 두번째 인자는 그 변수가 변하면 실행 할 함수
    watch(one, () => {

    });

    // computed 사용법
    let re = computed(() => {
      return follower.value.length;
    });
    console.log(re.value);
    
    // store 변수 사용법
    // Composition API에서는 mapState, mapMutations, mapActions 못씀..
    let store = useStore();
    console.log(store.state.age);
    // console.log(store.commit());

    // methods 사용법
    function searchUser(e) {
      if (e.target.value == '') {
        sFollower.value = [...follower.value];
      } else {
        sFollower.value = follower.value.filter(data => {
          return data.name.includes(e.target.value);
        });
      }
    }

    onMounted(() => {
      axios.get('/follower.json').then((result) => {
        follower.value = result.data;
        sFollower.value = [...follower.value];
      }).catch((error) => {
        console.log(error.message);
      });
    });

    return { follower, sFollower, searchUser };
  }
}
</script>

