<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>

    <ul class="header-button-right">
      <li v-if="step == 3" @click="writePost">게시</li>
      <li v-else @click="writePost">Next</li>
    </ul>

    <img src="./assets/logo.png" class="logo" />
  </div>

  <!-- <p>{{ 더보기데이터 }}</p> -->

  <!-- <h4>안녕 {{ $store.state.age }}</h4> -->
  <!-- <button @click="데이터가져오기()">버튼</button> -->

  <!-- <p>{{ $store.state.more }}</p>
  <button @click="$store.dispatch('getData')">더보기버튼</button> -->

  <Container @writeContent="tempData.content = $event" @writeName="tempData.name = $event"
    :step="step" :uploadImageURL="tempData.postImage" :filter="tempData.filter" />

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>

<script>
import Container from './components/Container.vue'
import { mapActions,  mapMutations,  mapState } from 'vuex'

export default {
  name: "App",
  data() {
    return {
      step: 0,
      tempData: {
        name: "",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: "",
        likes: 0,
        date: "May 15",
        content: "",
        filter: ""
      }
    };
  },
  mounted() {
    // mitt 라이브러리 사용 => 대부분은 mounted 안에 적음
    this.emitter.on('changeFilter', (filter) => {
      this.tempData.filter = filter;
    });
  },
  // 종속된 값에 의존함 => data(state) 속성에 정의한 반응형 데이터를 return하면 그 값에 종속되어 있는거임 => 그 값이 변하면 업데이트 됨
  // 종속되어 있지 않으면 재랜더링시에도 다시 실행되지 않음 => 업데이트 되지 않음
  computed: {
    name() { // computed에서 state 변수를 갖다 쓰는 가장 기초적인 방식
      return this.$store.state.name;
    },
    ...mapState([ // state 변수를 갖다 쓰고 싶을 때
      "name", "age", "more"
    ]),
    ...mapState({ // state 변수를 다른 이름으로 갖다 쓰고 싶을 때
      내이름: "name",
      더보기데이터: "more"
    })
  },
  // 종속되어 있냐를 상관하지 않고 재랜더링시에 계속 업데이트 됨 => 재호출됨
  methods: {
    ...mapMutations([ // mutations 함수를 갖다 쓰고 싶을 때
      "changeName"
    ]),
    ...mapMutations({ // mutations 함수를 다른 이름으로 갖다 쓰고 싶을 때
      나이업: "plusAge"
    }),
    ...mapActions([ // actions 함수를 갖다 쓰고 싶을 때
      "getData"
    ]),
    ...mapActions({ // actions 함수를 다른 이름으로 갖다 쓰고 싶을 때
      데이터가져오기: "getData"
    }),
    upload(e) { // 이미지 업로드 시 실행
      let inputFile = e.target.files;
      this.tempData.postImage = URL.createObjectURL(inputFile[0]);
      this.tempData.filter = "";
      this.step++;
    },
    writePost() { // 글 작성 버튼 클릭 시 실행
      if (this.step == 0) {
        return;
      }
      if (++this.step == 4) {
        this.$store.commit('writePost', {...this.tempData});
        this.step = 0;
      }
    }
  },
  components: {
    Container: Container
  }
};
</script>

