import { createStore } from 'vuex'
import instaData from './assets/instaData.js'
import axios from 'axios'

const store = createStore({
  state(){
    return {
      name: "kim",
      age: 20,
      data: [...instaData],
      more: {}
    }
  },
  mutations: {
    changeName(state) {
      state.name = "박";
    },
    plusAge(state, num) {
      state.age += num;
    },
    clickLikes(state, i) {
      if (!state.data[i].liked) { // 좋아요 누르지 않은 상태
        state.data[i].likes++;
        state.data[i].liked = true;
      } else { // 좋아요 누른 상태
        state.data[i].likes--;
        state.data[i].liked = false;
      }
    },
    writePost(state, postData) {
      state.data.unshift(postData);
    },
    setMore(state, data) {
      state.more = data;
    }
  },
  actions: { // { commit } 이런식으로 인자를 받으면 commit 함수를 바로 쓸 수 있음
    getData(context) { // context == $store
      axios.get("https://codingapple1.github.io/vue/more0.json").then((result) => {
        context.commit('setMore', result.data);
      });
    }
  }
});

export default store;